const displayData = (l1, l2, l3) => {
  const tab1 = l1.length === 0 ? [] : l1;
  const tab2 = l2.length === 0 ? [] : l2;
  const tab3 = l3.length === 0 ? [] : l3;
  console.log("tab2", tab1);
  return tab1.map((t1) => {
    return tab2.length === 0 ? (
      <li>{t1}</li>
    ) : (
      tab2.map((t2) => {
        return tab3.length === 0 ? (
          <li>{`${t1}, ${t2}`}</li>
        ) : (
          tab3.map((t3) => <li>{`${t1}, ${t2}, ${t3}`}</li>)
        );
      })
    );
  });
};

function ItemsList({ l1, l2, l3 }) {
  console.log(
    "lists",
    l1.map((l) => l)
  );

  console.log("displayData(l1, l2, l3)", displayData(l1, l2, l3));
  return displayData(l1, l2, l3);
}

export default ItemsList;
