import "./styles.css";
import MultiSelect from "./MultiSelect";
import { Button } from "@material-ui/core";
import { useState } from "react";
import ItemsList from "./ItemsList";

export default function App() {
  document.title = "Ta shniya hadi kob kob a kharbouch";
  const [firstCategoryItems, setFirstCategoryItems] = useState([]);
  const [secondCategoryItems, setSecondCategoryItems] = useState([]);
  const [thirdCategoryItems, setThirdCategoryItems] = useState([]);

  const [calculate, setCalculate] = useState(false);

  const getFirstCategoryItems = (selectedValues) =>
    setFirstCategoryItems(selectedValues);

  const getSecondCategoryItems = (selectedValues) =>
    setSecondCategoryItems(selectedValues);

  const getThirdCategoryItems = (selectedValues) =>
    setThirdCategoryItems(selectedValues);

  console.log("calculate", calculate);
  return (
    <div className="App">
      <div className="muiSelect">
        <MultiSelect
          categoryName={"category 1"}
          getCategoryItems={getFirstCategoryItems}
        />
        <MultiSelect
          categoryName={"category 2"}
          getCategoryItems={getSecondCategoryItems}
        />
        <MultiSelect
          categoryName={"category 3"}
          getCategoryItems={getThirdCategoryItems}
        />
      </div>
      <Button
        className="biyenLih"
        onClick={() => {
          setCalculate(!calculate);
          console.log(
            "firstCategoryItems",
            firstCategoryItems.map((firstItem) => firstItem)
          );
          console.log("secondCategoryItems", secondCategoryItems);
          console.log("thirdCategoryItems", thirdCategoryItems);
        }}
      >
        BIYEN LIYA
      </Button>
      {calculate && (
        <ul>
          <ItemsList
            l1={firstCategoryItems}
            l2={secondCategoryItems}
            l3={thirdCategoryItems}
          />
        </ul>
      )}
    </div>
  );
}
